export const toBeautifyValue =value=>{
    switch (value) {
        case 'Password':
            return ''
        case 'Category':
            return ''
        case 'Subcategory':
            return ''
        case 'Company':
            return ''
        case 'Edit':
            return ''
        case 'Delete':
            return ''
        case 'Create':
            return ''
        case 'Comment':
            return ''
        case 'City':
            return ''
        case 'Account':
            return ''
        case 'Update':
            return ''
        case 'Put':
            return ''
        case 'Subcategories':
            return ''
        default:
            return value
    }
}